import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {map} from 'rxjs/operators';
import * as moment from 'moment'

@Injectable({
  providedIn: 'root'
})
export class CovidDataService {

  constructor(
    private http: HttpClient
  ) {

  }
  getData(country){
    this.getHistory(country)
    var headers = new HttpHeaders({ "x-rapidapi-host":"coronavirus-monitor.p.rapidapi.com", "x-rapidapi-key": "8837c5d3c4msh517769883b6d693p1cf199jsn895aef8c65d1"})
    headers.append("x-rapidapi-key", "8837c5d3c4msh517769883b6d693p1cf199jsn895aef8c65d1");
    headers.append("x-rapidapi-host", "coronavirus-monitor.p.rapidapi.com");

    const url = "https://coronavirus-monitor.p.rapidapi.com/coronavirus/latest_stat_by_country.php?country="+country;
    return this.http.get(url,{headers: headers})
  }
  getHistory(country){
    var headers = new HttpHeaders({ "x-rapidapi-host":"coronavirus-monitor.p.rapidapi.com", "x-rapidapi-key": "8837c5d3c4msh517769883b6d693p1cf199jsn895aef8c65d1"})
    headers.append("x-rapidapi-key", "8837c5d3c4msh517769883b6d693p1cf199jsn895aef8c65d1");
    headers.append("x-rapidapi-host", "coronavirus-monitor.p.rapidapi.com");

    const url = "https://coronavirus-monitor.p.rapidapi.com/coronavirus/cases_by_particular_country.php?country="+country;
    return this.http.get(url,{headers: headers}).pipe(
      map(
        responseData=>{
          // console.log(responseData['stat_by_country'])
          for (var i in responseData['stat_by_country']) {
            responseData['stat_by_country'][i].record_date = moment(responseData['stat_by_country'][i].record_date).format('MM-DD-YYYY')
          }


          // Declare a new array
          let newArray = [];

          // Declare an empty object
          let uniqueObject = {};

          // Loop for the array elements
          for (let i in responseData['stat_by_country']) {

            // Extract the title
            let objTitle = responseData['stat_by_country'][i]['record_date'];

            // Use the title as the index
            uniqueObject[objTitle] = responseData['stat_by_country'][i];
          }
          // Loop to push unique object into array
          for (i in uniqueObject) {
             let totalCases = parseInt((uniqueObject[i]['total_cases']).replace(',', ''))
             let totaldeaths = parseInt((uniqueObject[i]['total_deaths']).replace(',', ''))
            newArray.push([uniqueObject[i]['record_date'],totalCases, totaldeaths]);
          }

          // Display the unique objects
          console.log(newArray);



    return newArray
        }
      )
    )
  }
  getStatesData(){
    return this.http.get('https://covid-91177.firebaseio.com/countByState.json')
      .pipe(
        map(
          responseData =>{
            const statesArray= [];
            for(const key in responseData){
              if(key === 'count'){
                responseData['count'] = responseData['value']
                delete responseData['count']
              }
               statesArray.push({... responseData[key]})
              // statesArray.push({...responseData[key]})
            }
            statesArray.sort(function (a, b) {
              return a.count - b.count;
            }).reverse();
            const statesArrayData = [];
            const mapData = [];
            for(const key in statesArray){
              let color = ''
              if(statesArray[key].count <= 5){
                color = 'green'
              }else if(statesArray[key].count <= 20){
                color = 'orange'
              }else if(statesArray[key].count >= 20){
                color = 'red'
              }
             statesArrayData.push([statesArray[key].name, statesArray[key].count, color, statesArray[key].count])
              mapData.push({id:statesArray[key].id, value:statesArray[key].count})
            }
            const result = {};
            result['statesArrayData'] = statesArrayData;
            result['mapdata'] = mapData;
            return result;
          }
        )
      )
  }
}
